<template>
  <div id="app">
    <Buttons v-if="kind==''"/>
    <Module ref="module" v-if="kind!=''" :kind="kind"/>
    <Photos v-if="photos_show" :kind="kind"/>
  </div>
</template>

<script>
import Buttons from "@/components/Buttons";
import Photos from "@/components/Photos";
import Module from "@/components/Module";

export default {
  name: 'App',
  components: {
    Module,
    Buttons,
    Photos
  },
  data() {
    return {
      photos_show: true,
      kind: this.judge_kind(),
    }
  },
  mounted: function() {
    console.log(this.kind)
    this.preload()
    setInterval(this.geta, 10);
  },
  methods: {
    geta: function (){
      this.photos_show = !(this.kind != '' && this.$refs.module.kind_phone_mod == 'show');
    },
    getQueryString: function(name){
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) return unescape(r[2]); return null;
    },
    judge_kind: function (){
      var kindd = this.getQueryString("kind");
      if (kindd!="Experience_sharing" && kindd!="Document_sharing" && kindd !="Medical_science_popularization" && kindd!="Psychological_words" && kindd!="Intensive_care" && kindd!="Medical_English" && kindd!="Department_culture" && kindd!="Video"){
        kindd="";
      }
      return kindd;
    },
    preload: function() {
      let imgs = [
        require('./assets/home_photos/1.jpg'),
        require('./assets/home_photos/2.jpg'),
        require('./assets/home_photos/3.jpg'),
        require('./assets/home_photos/4.jpg'),
        require('./assets/home_photos/5.jpg'),
      ]

      for (let img of imgs) {
        let image = new Image()
        image.src = img
        image.onload = () => {
          console.log(image);
        }
      }
    },
  },

}
</script>
