<template>
  <header >
    <div v-if="modkind=='pc'" :class="kind" class="label">
      <div v-for="(value,index) in allkind" :key="index" class="kind_title" :class="value=='科室宣传片' ? 'video_title_div' : ''">
        <a :href="'?kind='+allkind_english[index]"><p :class="value==title ? 'thiskind' : ''">&nbsp;{{value}}&nbsp;</p></a>
      </div>
    </div>
    <div :class="kind" class="title_img">
      <div @click="changekindstyle" class="phone_kind_icon" :class="'icon_'+modkind">
        <img v-if="kind_phone_mod=='hide' && darkmod=='light'" style="margin: 0px;" src="../assets/hide.svg"/>
        <img v-if="kind_phone_mod=='show' && darkmod=='light'" style="margin: 0px;" src="../assets/show.svg"/>

        <img v-if="kind_phone_mod=='hide' && darkmod=='dark'" style="margin: 0px;" src="../assets/hide_dark.svg"/>
        <img v-if="kind_phone_mod=='show' && darkmod=='dark'" style="margin: 0px;" src="../assets/show_dark.svg"/>
      </div>
      <div :class="kind=='Video' ? 'video' : 'other'" class = "all" id="all">
        <div class = "img next">
          <a href="./"><img v-if="darkmod=='light'" src="../assets/icon_with_title.png" alt="logo"/><img v-if="darkmod=='dark'" src="../assets/icon_with_title_dark.png" alt="logo"/></a>
        </div>
        <div ref="title_div" class="title next">
          <h1 class="title">{{title}}</h1>
        </div>
      </div>
    </div>
    <div v-if="kind_phone_mod=='show'" class="kind_all_phone">
      <a v-for="(kindd,index) in allkind" :key="index" :class="kindd==title ? 'thiskind' : ''" :href="'?kind='+allkind_english[index]"><li >{{kindd}}</li></a>
    </div>
  </header>
</template>

<script>
export default {
  name: "Module",
  props: {
    kind:String,
  },
  data(){
    return{
      kind_phone_mod:"hide",
      darkmod:"light",
      modkind : "pc",
      allkind : ["重说纷纭","科室文化","文献分享","医学科普","心理话","重症护理","医学英语", "科室宣传片"],
      allkind_english : ["Experience_sharing","Department_culture","Document_sharing","Medical_science_popularization",
        "Psychological_words","Intensive_care","Medical_English", "Video"],
      title:this.kind_str(this.kind),
      mediaQuery : window.matchMedia('(prefers-color-scheme: dark)'),
    }
  },
  mounted() {
    if (/ipad|iphone|midp|rv:1.2.3.4|ucweb|android|windows ce|windows mobile/.test(navigator.userAgent.toLowerCase())) {
      this.modkind="phone";
    }
    document.getElementById("all").style.width = (this.$refs.title_div.offsetWidth+95)+"px";
    setInterval(this.changeBackgroundColor, 10);
  },
  methods: {
    changekindstyle: function (){
      if (this.kind_phone_mod=="hide"){
        this.kind_phone_mod="show";
      }else{
        this.kind_phone_mod="hide";
      }
    },
    changeBackgroundColor: function(){
      var body = document.getElementById("body");
      var html = document.getElementById("body");
      if(!this.mediaQuery.matches){
        this.darkmod="light";
        if (this.kind_phone_mod=='hide'){
          body.style.setProperty('background-color','#F1F1F1');
          html.style.setProperty('background-color','#F1F1F1');
        }else {
          body.style.setProperty('background-color','#FdFdFd');
          html.style.setProperty('background-color','#FdFdFd');
        }
      }else {
        this.darkmod="dark";
        if (this.kind_phone_mod=='hide'){
          body.style.setProperty('background-color','#1f1f1f');
          html.style.setProperty('background-color','#1f1f1f');
        }else {
          body.style.setProperty('background-color','#0a0a0a');
          html.style.setProperty('background-color','#0a0a0a');
        }
      }
    },
    kind_str: function (kind){
      var dic = new Array();
      dic["Experience_sharing"]="重说纷纭";
      dic["Department_culture"]="科室文化";
      dic["Document_sharing"]="文献分享";
      dic["Medical_science_popularization"]="医学科普";
      dic["Psychological_words"]="心理话";
      dic["Intensive_care"]="重症护理";
      dic["Medical_English"]="医学英语";
      dic["Video"]="科室宣传片";
      return dic[kind];
    },
  }
}
</script>
<style scoped>
  a{
    color: #000000;
  }
  li{
    margin-left: 10px;
    margin-right: 10px;
    padding-left: 5px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 30px;
    list-style-type: none;
    border-bottom-style: solid;
    border-bottom-width: 2px;
    border-bottom-color: #cbccd2;
  }
  div.kind_all_phone{
    background: #fdfdfd;
    width: 100%;
  }
  p{
    font-weight: bold;
    color: #000000;
    font-size: 20px;
    margin: 0;
  }
  .thiskind{
    color: #0126b4;
  }

  div.label{
    background-color: #e9e9e9;
    width: 100%;
    height: 40px;
  }

  .Experience_sharing{
    background-image:linear-gradient(45deg, #a4f1e3, #c3caec);
  }
  .Department_culture{
    background-image:linear-gradient(45deg,#EDD4EB,#E2B0FF);
  }
  .Document_sharing{
    background-image:linear-gradient(45deg,#DBF8FF,#5EFCE8);
  }
  .Medical_science_popularization{
    background-image:linear-gradient(45deg,#FEE8E8,#FAD7A1,#E96D71);
  }
  .Psychological_words{
    background-image:linear-gradient(45deg,#D1EBE2,#79F1A4);
  }
  .Intensive_care{
    background-image:linear-gradient(45deg,#FAFFDE,#FDEB71);
  }
  .Medical_English{
    background-image:linear-gradient(45deg,#DAFFD8,#90F7EC);
  }
  .Video{
    background-image:linear-gradient(45deg,#DDE1FF,#CE9FFC);
  }

  div.phone_kind_icon{
    display: block;
    border-radius:10%;
    padding: 1px;
    padding-top: 2px;
    padding-bottom: 2px;
    margin-left: 3%;
    margin-top: 35px;
    height: 30px;
  }

  div.kind_title{
    border: 1px solid;
    border-color: #cccccc;
    background-color: #dadada;
    margin-top: 5px;
    margin-right: calc(calc(100% - 755px)/16);
    margin-left: calc(calc(100% - 755px)/16);
  }
  div.video_title_div{
    margin-right: 0px;
  }

  div.label{
    border: none;
    border-bottom: 2px solid;
    border-bottom-color: #676767;
    display: none;
  }

  header{
    width: 100%;
    height: 100px;
    margin: 0px auto;
  }

  @media only screen and (min-width: 780px){
    header{
      height: 140px;
    }
    div.phone_kind_icon{
      display: none;
    }
    div.label{
      display: block;
    }
  }
  div.icon_phone{
    display: block;
  }

  h1.title{
    font-weight: bold;
    color: #000000;
    margin-top: 2px;
    font-size: 70px;
  }

  div.title_img{
    width: 100%;
    height: 100px;
  }
  div.all{
    left: 50%;
    text-align: center;
    height: 100px;
    margin: auto;
  }

  div.next,.kind_title,div.back,div.phone_kind_icon{
    float: left;
  }
  div.title{
    margin-top: auto;
    margin-left: 10px;
    height: 70px;
    display: inline-block;
  }

  img{
    margin-left: 5%;
    display: block;
    height: 100%;
    width: auto;
  }
  div.img{
    margin:5px auto;
    width: auto;
    height: 90%;
    display: inline-block;
  }
  div.video{
    width:435px
  }
  @media screen and (prefers-color-scheme: dark) {
    li{
      background-color: #0a0a0a;
    }
    div.kind_title{
      background-color: #313131;
    }
    div.kind_all_phone{
      background-color: #0a0a0a;
    }
    p,a{
      color: #FFFFFF;
    }
    .thiskind{
      color: #6b87ff;
    }
    .Experience_sharing{
      background-image:linear-gradient(45deg, #175b4e, #1a286c);
    }
    .Department_culture{
      background-image:linear-gradient(45deg,#70237B,#2c0629);
    }
    .Document_sharing{
      background-image:linear-gradient(45deg,#1D6B72,#062b35);
    }
    .Medical_science_popularization{
      background-image:linear-gradient(45deg,#AB5C5C,#442f2f);
    }
    .Psychological_words{
      background-image:linear-gradient(45deg,#377238,#285847);
    }
    .Intensive_care{
      background-image:linear-gradient(45deg,#59612c,#B8C141);
    }
    .Medical_English{
      background-image:linear-gradient(45deg,#2B8452,#022b00);
    }
    .Video{
      background-image:linear-gradient(45deg,#613589,#13194D);
    }
    h1.title{
      color: #FFFFFF;
    }
  }
</style>